@import url(https://fonts.googleapis.com/css?family=Roboto:500);

/* General Styles */
.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

/* Input Styles */
input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-bottom: 20px;
}

/* City Search Styles */
.city {
  width: 250px;
}

#city-search {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

/* Event Styles */
.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
  transition: all 0.3s ease;
}

.event:hover {
  background-color: #e9f5f5;
  border-color: #bcdcdb;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

/* Event Details Styling */
#details {
  background-color: #f2f2f2;
  padding: 10px;
  border-radius: 4px;
  margin-top: 10px;
}

#details p {
  margin: 5px 0;
  font-weight: bold;
}

#details span {
  font-weight: normal;
  color: #666;
}

/* Number of Events Styles */
#number-of-events {
  margin-bottom: 20px;
}

.number-input {
  width: 100px;
  margin: 0 auto;
}

/* Responsive Design */
@media (max-width: 768px) {
  .city,
  .suggestions {
    width: 100%;
  }

  .event,
  #number-of-events {
    padding: 10px;
  }
}
